import Vue from 'vue'
import Router from 'vue-router'

Vue.use(Router)

export default new Router({
  routes: [
    {path:"/",redirect:"/index"},
    {
      path:'/indexz',
      redirect:'/indexz/indexnpassword',
      component:resolve=>{require(['@/components/index_z'],resolve)},
      children:[
        {path:'indexnpassword',component:resolve=>{require(['@/page/index_npassword'],resolve)}},
        {path:'indexRole',component:resolve=>{require(['@/page/index_role'],resolve)}},
        {path:'indexStaff',component:resolve=>{require(['@/page/index_staff'],resolve)}}
      ]
    },
    {path:'/indexNpassword',component:resolve=>{require(['@/page/index_npassword'],resolve)}},
    {path:'/indexZc',component:resolve=>{require(['@/page/index_zc'],resolve)}},
    {path:'/index',component:resolve=>{require(['@/components/index'],resolve)}},
    {path:'/motorcade',redirect:"/motorcade/mycar",component:resolve=>{require(['@/page/motorcade'],resolve)},
  		children:[
  			{
          path:'/motorcade/mycar',
          component:resolve=>{require(['@/page/children/mycar'],resolve)},
          children:[
            {path:'/motorcade/mycar/tx',component:resolve=>{require(['@/page/LYL-btn-message1'],resolve)}},
          ]
        },
  			{path:'/motorcade/cardsearch',component:resolve=>{require(['@/page/children/cardsearch'],resolve)}},
        {path:'/motorcade/sharegl',component:resolve=>{require(['@/page/children/sharegl'],resolve)}},
        {path:'/motorcade/renzheng',component:resolve=>{require(['@/page/children/renzheng'],resolve)}},
        {path:'/motorcade/newcar',component:resolve=>{require(['@/page/children/newcar'],resolve)}},
        {path:'/motorcade/carshare',component:resolve=>{require(['@/page/children/carshare'],resolve)}},
  			{path:'/motorcade/history',component:resolve=>{require(['@/page/children/history'],resolve)}},
        {path:'memberview',component:resolve=>{require(['@/page/memberview'],resolve)}},
        {path:'message',component:resolve=>{require(['@/page/LYL-btn-message'],resolve)}},
        {path:'/motorcade/two',component:resolve=>{require(['@/page/children/two'],resolve)}}
  		]
  	},
    {path:'/waybill',component:resolve=>{require(['@/page/waybill'],resolve)}},
    {
      path:'/payMoney',
      component:resolve=>{require(['@/page/payMoney'],resolve)},
      children:[
        // {path:'errorlogging',component:resolve=>{require(['@/page/LYL-ErrorLogging'],resolve)}},
      ]
    },
    {path:'/errorlogging',component:resolve=>{require(['@/page/LYL-ErrorLogging'],resolve)}},
    {path:'/debitNode',component:resolve=>{require(['@/page/debitNode'],resolve)}},
    {path:'/factoring',component:resolve=>{require(['@/page/factoring'],resolve)}},
    {path:'/freight',component:resolve=>{require(['@/page/freight'],resolve)}},
    // 资费说明
     {path:'/jwdzfsm',component:resolve=>{require(['@/page/jwdzfsm'],resolve)}},
    //  收资明细
      {path:'/jwdszmx',component:resolve=>{require(['@/page/jwdszmx'],resolve)}},
      //  消费明细
      {path:'/jwdxfmx',component:resolve=>{require(['@/page/jwdxfmx'],resolve)}},
      // 充值
      {path:'/jwdchongzhi',component:resolve=>{require(['@/page/jwdchongzhi'],resolve)},
        children:[
          {path:'/jwdchongzhi',redirect:'/jwdchongzhi/qycenter'},
         {path:'qycenter',component:resolve=>{require(['@/page/qycenter'],resolve)}},
         {path:'password',component:resolve=>{require(['@/page/password'],resolve)}}
        ]
    },
      // 提现
      {path:'/jwdtixian',component:resolve=>{require(['@/page/jwdtixian'],resolve)}},
      // 消息盒子
      {path:'/jwdxxhz',component:resolve=>{require(['@/page/jwdxxhz'],resolve)}},
       // 提现下一步
      {path:'/jwdtixian2',component:resolve=>{require(['@/page/jwdtixian2'],resolve)}},
      {path:'/jwdtixian3',component:resolve=>{require(['@/page/jwdtixian3'],resolve)}},
      {path:'/jwdtixian4',component:resolve=>{require(['@/page/jwdtixian4'],resolve)}},
      {path:'/jwdtixian5',component:resolve=>{require(['@/page/jwdtixian5'],resolve)}},
    {path:'/one',component:resolve=>{require(['@/page/children/one'],resolve)}},
    {path:'/three',component:resolve=>{require(['@/page/children/three'],resolve)}},
    {path:'/four',component:resolve=>{require(['@/page/children/four'],resolve)}}
  ]
})



